// btn-lemon
.item-rounded-corner {
    a {
        svg {
            stroke: $btn_lemon_bg_color;
        }

        &:hover {
            svg {
                animation-play-state: running;
                stroke: $btn_lemon_bg_color;
            }
        }
    }
}

.bt-lemon-container {
    a {
        background-color: $btn_lemon_bg_color;
        color: $btn_lemon_text_color;

        &:hover {
            color: $btn_lemon_text_color;
        }
    }
}

.btn-custom {
    background-color: $main_color;
    color: $colors-white;
    border: 1px solid $main_color;
}